<template>
  <section>
    <nav class="navbar">
      <a class="navbar-brand">Package Request</a>
    </nav>
    <div class="container">
      <div class="wrap jcc mt-2">
        <div class="col-blog" v-for="(blog, index) in blogs" :key="index">
          <div class="card">
            <p class="blog-title">Name: {{ blog.name }}</p>
            <p class="blog-description">Email: {{ blog.email }}</p>
            <p class="blog-description">Description: {{ blog.description }}</p>
            <p class="blog-description">Package: {{ blog.pachet }}</p>
            <div class="button-card">
              <vs-button 
                danger
                gradient
                type="button" 
                @click="deleteAPI(blog._id)">
                Delete</vs-button>
              <vs-button
                color="rgb(59,222,200)"
                gradient
              >
                <i class="bx bxs-purchase-tag"></i>Done
              </vs-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  name: 'Header',
  data() {
    return {
      blogs: [],
    };
  },
  methods: {
    deleteAPI(_id){
      axios
        .delete("https://charitablebedatabase.herokuapp.com/posts/"+_id)
        .then((response) => {
          console.log(response);
          window.location.reload()
        })
        .catch((error) => {
          console.log(error);
      });
    }
  },
  created() {
    this.$http.get("https://charitablebedatabase.herokuapp.com/posts/").then(function (data) {
      this.blogs = data.body.slice(0);
    });
  },
};
</script>

